import React, { useEffect, useState } from 'react';
import { /* Card, CardBody, */ Row } from 'reactstrap';
import { Wizard, /* Steps, Step */} from 'react-albus';
import { connect } from 'react-redux';
import { addProfileData } from '../../../redux/actions';
import { NotificationManager } from '../../../components/common/react-notifications';
import ProfileForm from './profile-form';
import { Colxx } from '../../../components/common/CustomBootstrap';
// import TopNavigation from '../../../components/wizard/TopNavigation';
// import ThankYouForm from './thank-you-form';
// import BottomNavigation from '../../../components/wizard/BottomNavigation';

const CompleteRegistration = ({
  history,
  loading,
  error,
  currentUser,
  addProfileAction,
  message,
}) => {
  const [orgName] = useState('');
  const [fullName] = useState('');
  const [contactNo] = useState('');
  const [about] = useState('');
  useEffect(() => {
    if (error) {
      NotificationManager.error(
        error,
        'Profile Add Error',
        3000,
        null,
        null,
        ''
      );
    }
    if (!loading && message === 'profile-add-success') {
      NotificationManager.success(
        'Click next',
        'Profile Added Successfully',
        3000,
        null,
        null,
        ''
      );
    }
  }, [loading, error, message]);

  const initialValues = {
    orgName,
    fullName,
    contactNo,
    about
  };

  // const topNavClick = (stepItem, push) => {
  //   push(stepItem.id);
  // };

  // const onClickNext = (goToNext, steps, step) => {
  //   // eslint-disable-next-line no-param-reassign
  //   step.isDone = true;
  //   if (steps.length - 1 <= steps.indexOf(step)) {
  //     return;
  //   }
  //   goToNext();
  // };

  return (
    <Row className="h-100">
      <Colxx xxs="12" md="8" className="mx-auto my-auto wizard wizard-default">
        <Wizard>
       
              <ProfileForm
                addProfileAction={addProfileAction}
                history={history}
                currentUser={currentUser}
                initialValues={initialValues}
                loading={loading}
                message={message}
              />

            {/* <Card className="mt-4">
            <CardBody>
              <BottomNavigation
                className="justify-content-end"
                onClickNext={onClickNext}
                nextLabel="Next"
                showPrevious={false}
              />
            </CardBody>
          </Card> */}
          
            {/* <Step id="thank-you">
              <ThankYouForm 
                currentUser={currentUser}
              />
            </Step> */}
          


        </Wizard>
      </Colxx>
    </Row>
  );
};
const mapStateToProps = ({ authUser }) => {
  const { loading, error, message, currentUser } = authUser;
  return { loading, error, message, currentUser };
};

const mapActionToProps = {
  addProfileAction: addProfileData,
};

export default connect(mapStateToProps, mapActionToProps)(CompleteRegistration);
