import React from 'react';
import { Formik, Form, Field,useField } from 'formik';
import {
  Card,
  CardBody,
  FormGroup,
  Label,
  Button,
  CardTitle,
} from 'reactstrap';
// import { useAsync } from 'react-async-hook';
import IntlMessages from '../../../helpers/IntlMessages';
// import { getProfileDataAsync,getProfileData } from '../../../helpers/Utils';

const validateName = (value) => {
  let error;
  
  if (!value) {
    error = 'Please enter your name';
  } else if (value.length < 2) {
    error = 'Value must be longer than 2 characters';
  }
  return error;
};

const validatePhoneNumber = (value) => {
  let error;
  if (!value) {
    error = 'Please enter your phone number.';
  } else {
    const phone = value.replace(/[^0-9]/g, '');
    if (phone.length > 13) {
      error = 'Invalid Number, Enter 13 digits.';
    }
  }
  return error;
};
const MyTextArea = ({ ...props}) => {
  // useField() returns [formik.getFieldProps(), formik.getFieldMeta()]
  // which we can spread on <input> and alse replace ErrorMessage entirely.
  const [field, meta] = useField(props);
  return (
      <>
         
          <textarea className="text-area" {...field} {...props} />
          {meta.touched && meta.error ? (
              <div className="error">{meta.error}</div>
          ) : null}
      </>
  );
};

const ProfileForm = ({
  addProfileAction,
  history,
  currentUser,
  initialValues,
  loading,
}) => {
  const onAddProfile = (values) => {
    const { id, phone_number: phoneNumber } = currentUser;
    const profile = {
      ...values,
      userID: id,
      contactNo: phoneNumber,
    };

    addProfileAction({ profile, history });
  };

  return (
    <Card>
      <CardBody className="wizard-basic-step">
        <CardTitle className="mb-4">
          <h2 className="lead">
            <IntlMessages id="user.profile-form" />
          </h2>
        </CardTitle>
        <Formik initialValues={initialValues} onSubmit={onAddProfile}>
          {({ errors, touched }) => (
            <Form className="av-tooltip tooltip-label-right">
              <FormGroup>
                <Label>
                  <IntlMessages id="user.org-name" />
                </Label>
                <Field
                  className="form-control"
                  name="orgName"
                  validate={validateName}
                />
                {errors.name && touched.name && (
                  <div className="invalid-feedback d-block">{errors.name}</div>
                )}
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="user.full-name" />
                </Label>
                <Field
                  className="form-control"
                  name="fullName"
                  validate={validateName}
                />
                {errors.name && touched.name && (
                  <div className="invalid-feedback d-block">{errors.name}</div>
                )}
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="Contact Number" />
                </Label>
                <Field
                  className="form-control"
                  name="contactNo"
                  validate={validatePhoneNumber}
                />
                {errors.contactNo && touched.contactNo && (
                      <div className="invalid-feedback d-block">
                        {errors.contactNo}
                      </div>
                    )}
              </FormGroup>

          <FormGroup>
            <Label>
              <IntlMessages id="Organization Bio" />
            </Label>
           
            <MyTextArea 
               className="form-control"
               name="about"
               rows="6"
            />
                  
            {errors.name && touched.name && (
                  <div className="invalid-feedback d-block">{errors.name}</div>
                )}
          </FormGroup>

              <div className="d-flex justify-content-center align-items-center">
                <Button
                  color="primary"
                  type="submit"
                  className={`btn-shadow btn-multiple-state ${
                    loading ? 'show-spinner' : ''
                  }`}
                  size="lg"
                >
                  <span className="spinner d-inline-block">
                    <span className="bounce1" />
                    <span className="bounce2" />
                    <span className="bounce3" />
                  </span>
                  <span className="label">
                    <IntlMessages id="forms.submit" />
                  </span>
                </Button>
              </div>
            </Form>
          )}
        </Formik>
      </CardBody>
    </Card>
  );
};

export default ProfileForm;
